:root {
    --footerBackground: rgb(148,0,211)
}
.footer {
    width: 100%;
    height: 200px;
    background: var(--footerBackground);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
    margin-top: 200px;
  }
  
  .socialMedia svg {
    color: white;
    margin: 1rem;
    font-size: 2rem;
    cursor: pointer;
  }
  
  .footer p {
    color: white;
  }
  
  @media only screen and (max-width: 600px) {
    .footer svg {
      font-size: 2rem;
    }
  }